@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
/** Media Queries **/
.create .alert {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
}
.create .alert .err, .create .alert .enviado {
  padding: 0.7rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  font-weight: bold;
  width: 100%;
  text-align: center;
}
.create .alert .err {
  background-color: #ff2d2d;
}
.create .alert .enviado {
  background-color: #71B100;
}
.create .button {
  background-color: #71B100;
  color: #FFFFFF;
}
.create .button:hover {
  background-color: #E08709;
}